import "./index.css";

const UserList = (props) => {
  const { userDetails, setActiveUserId } = props;
  const { name, id } = userDetails;

  const onButtonClick = () => {
    setActiveUserId(id);
  };

  return (
    <>
      <li className="username-details">
        <button type="button" className="user-name" onClick={onButtonClick}>
          {name}
        </button>
      </li>
    </>
  );
};

export default UserList;
