class App extends Component {
  state = { activeUserId: userDetailsList[0].id };

  setActiveUserId = (id) => {
    this.setState({ activeUserId: id });
  };

  render() {
    const { activeUserId } = this.state;

    const { name, phone, bio, imageUrl } = userDetailsList[activeUserId];

    return (
      <div className="App">
        <div className="user-container">
          <ul>
            {userDetailsList.map((eachUser) => (
              <UserList
                userDetails={eachUser}
                key={eachUser.id}
                isActive={activeUserId === eachUser.id}
                setActiveUserId={this.setActiveUserId}
              />
            ))}
          </ul>
        </div>
        <div className="bio-container">
          <div className="name-container">
            <img src={imageUrl} alt="userimage" className="bio-image" />
            <div className="phone-name">
              <h1 className="name-colour">{name}</h1>
              <h1 className="number">{phone}</h1>
            </div>
          </div>
          <div>
            <p className="bio-data">{bio}</p>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
